import { _decorator, Component, Node, random, randomRangeInt } from 'cc';
const { ccclass, property } = _decorator;

//随机运动
@ccclass('sgj_normal')
export class sgj_normal extends Component {

    curIndex = 0;
    is_start = false;

    start() {

    }

    play() {
        this.is_start = true;
        this.curIndex = randomRangeInt(1, 6);
    }

    update(deltaTime: number) {

        if (!this.is_start) return;

        for (let i = 0; i < 24; i++) {
            if (i % 6 == this.curIndex) {
                globalThis.sgj_run.mask_node[i].active = true;
            }
            else {
                globalThis.sgj_run.mask_node[i].active = false;
            }

        }

    }
}

